/* -------------- GENERAL -------------- */
body {
	font-family: arial, geneva, sans-serif;
	margin-top: 145px; }

.content {
	margin: 0 auto;
	min-height: 400px;
	width: 980px; }


.hide {
	position: absolute;
	text-indent: -9999px;
	left: -9999px; }

.center {
	text-align: center; }

.img-responsive {
	height: auto;
	width: 100%; }

b {
	font-weight: bold; }

.clearfix:after {
	display: table;
	content: "";
	clear: both; }

.content {
	h2 {
		text-align: center;
		margin: 20px 0 30px;
		background-color: #303030;
		color: #fff;
		padding: 10px 0;
		font-size: 24px;
		position: relative;
		font-family: Cabin, sans-serif, Arial, Verdana, Geneva;
		&:after {
			content: "";
			position: absolute;
			width: 96%;
			height: 10px;
			background-color: transparent;
			left: 2%;
			bottom: 0;
			z-index: -1;
			border-radius: 0 0 50% 50%;
			box-shadow: 0 5px 10px rgba(0,0,0,.5); } } }
