#offices {
	.wrapper {
		position: relative;
		margin-bottom: 30px; }

	img {
		position: relative;
		z-index: 0; }

	.information {
		width: 430px;
		position: absolute;
		top: 40px;
		left: 50px;
		z-index: 10; }

	h3 {
		color: #303030;
		font-family: Cabin, Arial, sans-serif;
		font-size: 36px;
		text-align: center; }

	h3 + p {
		color: #41403d;
		font-family: Cabin, Arial, sans-serif;
		font-size: 14px;
		text-align: center;
		line-height: 22px; } }
